<template>
    <div class="music-list">
        <div class="back" @click="back">
            <i class="icon-back"></i>
        </div>
        <h1 class="title" v-html="title"></h1>
        <div class="bg-image" :style="bgStyle" ref="bgImage">
            <div class="play-wrapper">
                <div class="play" ref="playBtn" v-show="songs.length>0" @click="random">
                    <i class="icon-play"></i>
                    <span class="text">随机播放全部</span>
                </div>
            </div>
            <div class="filter" ref="filter"></div>
        </div>
        <div class="bg-layer" ref="layer"></div>
        <scroll :data="songs"
                    @scroll="scroll"
                    :listen-scroll="listenScroll"
                    :probe-type="probeType"
                    class="list"
                    ref="list" >
            <div class="song-list-wrapper">
                <song-list :rank="rank" @selectItem="select" :songs = "songs"></song-list>
            </div>
            <div class="loading-container" v-show="!songs.length">
                <loading></loading>
            </div>
        </scroll>

    </div>
</template>

<script>
    import Scroll from 'base/scroll/scroll'
    import Loading from 'base/loading/loading'
    import SongList from 'base/song-list/song-list'
    import { prefixStyle} from "common/js/dom"

    import { mapActions } from 'vuex'
    import {playlistMixin} from 'common/js/mixin'
    const RESERVED_HEIGHT = 40
    const transform = prefixStyle('transform')
    const backdrop = prefixStyle("backdrop-filter")
	
    export default{
    	mixins:[playlistMixin],
        props:{
            bgImage:{
                type:String,
                default:''
            },
            songs:{
                type:Array,
                default:[]
            },
            title:{
                type:String,
                default:''
            },
            rank:{
            	type:Boolean,
            	default:false
            }
        },
        data(){
            return {
                scrollY : 0 //定义滚动距离
            }
        },
        computed:{
            bgStyle(){
                return `background-image:url(${this.bgImage})`
            }
        },
        created(){
            this.probeType = 3
            this.listenScroll = true  //listenScroll当滚动的时候触发scroll方法 并且将坐标pos传递给父组件
        },
        mounted(){
            this.imageHeight = this.$refs.bgImage.clientHeight
            this.minY = -this.imageHeight + RESERVED_HEIGHT  //定义最大的可移动的距离
            this.$refs.list.$el.style.top = `${this.imageHeight}px`  //让遮罩的top值正好等于image的高度。

        },
        components:{
            Scroll,
            Loading,
            SongList
        },
        methods:{
        	handlePlayList(playlist){
        		const bottom = playlist.length ? '60px' : 0
        		this.$refs.list.$el.style.bottom = bottom
        		this.$refs.list.refresh()
        	},
        	random(){
        		this.randomPlay({
        			list:this.songs
        		})
        	},
            scroll(pos) {
                this.scrollY = pos.y
            },
            back(){
                this.$router.back()
            },
            select(song,index){
                this.selectPlay({
                    list:this.songs,   //将整个列表作为参数传递 而不是仅仅传递点击的歌曲。
                    index
                })
            },
            ...mapActions([
                'selectPlay',
                'randomPlay'
            ])


        },
        watch:{
            //监听滚动的距离变化
            scrollY(newVal) {
                let translateY = Math.max(this.minY,newVal ) //因为向上滚动的时候为负值所以newVal会越来越小 所以在滚动到一定位置的时候就不会遮罩层layer就不会再动了
                let scale = 1
                let zIndex = 0
                let blur = 0
                const percent = Math.abs(newVal / this.imageHeight)
                if(newVal > 0){
                    scale = 1 + percent  //这里特别巧妙因为，当往下拉的时候imageHeight*percent正好等于newVal即往下拉的距离 这样就可以做到无缝拉动
                    this.$refs.list.$el.style.top = this.imageHeight + newVal + 'px'
                }else {
                    blur = Math.min(20,percent*20)
                }
               // console.log(translateY)
                this.$refs.layer.style[transform] =  `translate3d(0,${translateY}px,0)`  //layer这样设计一定会跟着走 只是在网上滚动的时候会停止在最小值那里
                this.$refs.filter.style[backdrop] = `blur(${blur}px)`


                if(newVal < this.minY) {
                    zIndex = 10         //层级是为了在向上滚动的时候将歌单隐藏掉
                    this.$refs.bgImage.style.paddingTop = 0
                    this.$refs.bgImage.style.height = `${ RESERVED_HEIGHT }px`
                    this.$refs.playBtn.style.display = "none"
                } else {
                    this.$refs.bgImage.style.paddingTop="70%"
                    this.$refs.bgImage.style.height = 0
                    this.$refs.playBtn.style.display=""
                }

                this.$refs.bgImage.style[transform] = `scale(${scale})`
                this.$refs.bgImage.style.zIndex = zIndex
            }
        }
    }
</script>
<!--告知webpack ~common是相对common然后自己配置了common这个路径是绝对路径-->
<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"
    @import '~common/stylus/mixin'
    .music-list
        position:fixed
        z-index:100
        top:0
        left:0
        bottom:0
        right:0
        background: $color-background
        .back
            position:absolute
            top:0
            left: 6px
            z-index:50
            .icon-back
                display:block
                padding: 10px
                font-size:$font-size-large-x
                color: $color-theme
        .title
            position:absolute
            top:0
            left:10%
            z-index:40
            width:80%
            no-wrap()
            text-align:center
            line-height: 40px
            font-size: $font-size-large
            color:$color-text
        .bg-image
            position:relative
            width:100%
            height:0
            padding-top:70%
            transform-origin:top
            background-repeat:no-repeat
            background-position:top center
            background-size:cover
            .play-wrapper
                position:absolute
                bottom: 20px
                z-index:50
                width:100%
                .play
                    box-sizing:border-box
                    width: 135px
                    padding:7px 0
                    margin:0 auto
                    text-align:center
                    border:1px solid $color-theme
                    color:$color-theme
                    -webkit-border-radius: 100px
                    -moz-border-radius: 100px
                    border-radius: 100px
                    font-size:0
                    .icon-play
                        display:inline-block
                        vertical-align:middle
                        margin-right: 6px
                        font-size: $font-size-medium-x
                    .text
                        display:inline-block
                        vertical-align:middle
                        font-size: $font-size-small
            .filter
                position: absolute
                top:0
                left:0
                width:100%
                height:100%
                background:rgba( 7,17,27,0.4)
        .bg-layer
            position relative
            height:100%
            background:$color-background
        .list
            position: fixed
            top:0
            bottom:0
            width:100%
            background:$color-background
            .song-list-wrapper
                padding:20px 20px
            .loading-container
                position :absolute
                width:100%
                top:50%
                transform:translateY(-50%)

</style>


